import { Fragment, useEffect } from 'react'
import { Card, CardBody, Row, Col } from 'reactstrap' //CardText, CustomInput Button
import coin from '../../../assets/images/boomer11sports/coin/coin.svg'
// import cricket from '../../../assets/images/boomer11sports/Cricket-yellow.svg' 
// import football from '../../../assets/images/boomer11sports/Football-yellow.svg'
// import basketball from '../../../assets/images/boomer11sports/Basketball-yellow.svg'
import { useParams, useRouteMatch } from 'react-router-dom'
import { getUserProfile, resetUserProfile, fetchpage } from '../store/actions'
import { useDispatch, useSelector } from 'react-redux'
import BreadCrumbs from '@components/breadcrumbs'
import Table from './Table'
import Moment from "moment"
import {
  userContents,
  userPrizes,
  userFixtures,
  userReferral,
  userTransactions
  // UserMoney
  // userReportStatus
} from './columns'
import { nameFirstLetters, getAddress } from '../constants'
import {
  //DollarSign,
  //Gift,
  //TrendingUp,
  // User,
  Phone,
  // Flag,
  Star,
  Calendar,
  Users,
  Globe,
  Key,
  // Mail,
  Check,
  // Bell,
  MapPin,
  TrendingUp
} from 'react-feather'
import '@styles/react/apps/app-users.scss'
import UiLoader from '@components/ui-loader'
import './styles.scss'
import Flag from 'react-world-flags'

const Profile = () => {
  const params = useParams()
  console.log((params.page))
  const dispatch = useDispatch()
  const match = useRouteMatch()
  const userURLID = match.params.id
  const store = useSelector((state) => state.users.userDetails)
  console.log(store)
  useEffect(() => {
    dispatch(getUserProfile(userURLID))
    dispatch(fetchpage(params.page))
    return () => {
      dispatch(resetUserProfile())
    }
  }, [])

  const ProfileCard = ({ userInfo, userMoney }) => {
    if (!userInfo) {
      return (
        <Card>
          <CardBody>
            <p>User information not available</p>
          </CardBody>
        </Card>
      )
    }

    const {
      email,
      full_name,
      avatar,
      joined_on,
      last_login,
      activated,
      language_pref,
      sex,
      // active,
      // u_id,
      mobile,
      dob,
      fav_team,
      address1,
      address2,
      city,
      state,
      country,
      zipcode
    } = userInfo
    return (
      <Card style={{ fontSize: '12px' }}>
        <CardBody>
          <Row className='w-100'>
            <div className="d-flex flex-column border-container-lg col-lg-12 col-xl-5">
              <div className="d-flex justify-content-start">
                <div className="avatar rounded profile-avatar-wrapper bg-light-secondary mr-1">
                  {avatar ? (
                    <img src={avatar} />
                  ) : (
                    <span className="avatar-content">
                      {full_name && full_name.trim()
                        ? nameFirstLetters(full_name)
                        : ''}
                    </span>
                  )}
                </div>
                <Col>
                <Row className='text-truncate ml-1'>{full_name}</Row>
                <Row className='text-truncate ml-1'>{email}</Row>
                </Col>
               
              </div>
              {userMoney && userMoney?.length && (
                <div className="d-flex align-items-center user-total-numbers">
                  <div className="d-flex align-items-center mr-2">
                    
                  </div>
                  
                  <div className="d-flex align-items-center">
                  </div>
                </div>
              )}
              <div>
                <Row>
                  <h4 className='mt-2'>User Money</h4>
                </Row>
                <Row className='mt-2'>
                  <Col xl={4} className='p-0'>
                    <Row xl={12}>
                      <Col xl={4}><img src={coin} style={{ width: '30px' }} /></Col>
                      <Col xl={8}>{store[0]?.user_money[0].total_balance}</Col>
                      <Col xl={12}>Total Balance</Col>
                    </Row>
                  </Col>
                  <Col xl={4} className='p-0'>
                    <Row xl={12}> 
                      <Col xl={4}><TrendingUp size={20} className='text-success' style={{ width: '30px' }} /></Col>
                      <Col xl={8}>{store[0]?.user_money[0].winnings}</Col>
                      <Col xl={12}>Winnings</Col>
                    </Row>
                  </Col>
                  <Col xl={4} className='p-0'>
                    <Row xl={12}> 
                      <Col xl={4}><img src={coin} style={{ width: '30px' }} /></Col>
                      <Col xl={8}>{store[0]?.user_money[0].bonus || '-'}</Col>
                      <Col xl={12}>Bonus</Col>
                    </Row>
                  </Col>
                </Row>
                <Row>
                  <div className='d-flex align-items-center mr-2 mt-1'>
                  </div>
                </Row>
              </div>
              <div className='d-flex align-items-center user-total-numbers'>
              </div>
            </div>
            <div className="mt-2 mt-xl-0 col-lg-12 col-xl-7">
              <div className="user-info-wrapper">
                <Row>
                  <Row className='w-100 ml-1'>
                    <h4>User Details</h4>
                  </Row>
                  <Col xl={7} className='p-0'>
                    {/* <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><User width="12" height="12" className="mr-1" />User Id</Col>
                      <Col xl={6} className='text-truncate '>{u_id || '-'}</Col>
                    </Row>
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><Mail width="12" height="12" className="mr-1" />Email</Col>
                      <Col xl={6} className='text-truncate '>{email || '-'}</Col>
                    </Row> */}
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><Key width="12" height="12" className="mr-1" />Date of Birth</Col>
                      <Col xl={6} className='text-truncate '>{dob === "" ? '-' : Moment((dob), 'DD MM YYYY').format("DD MMM YYYY")}</Col>
                    </Row>
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><Users width="12" height="12" className="mr-1" />Gender</Col>
                      <Col xl={6} className='text-truncate '>{sex || '-'}</Col>
                    </Row>
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><Star width="12" height="12" className="mr-1" />Favourite Team</Col>
                      <Col xl={6} className='text-truncate '>{fav_team || '-'}</Col>
                    </Row>
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><Calendar width="12" height="12" className="mr-1" />Joined On</Col>
                      <Col xl={6} className='text-truncate '>{Moment((joined_on), 'DD-MM-YYYY').format("DD MMM YYYY") || '-'}</Col>
                    </Row>
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><Calendar width="12" height="12" className="mr-1" />Last Login</Col>
                      <Col xl={6} className='text-truncate '>{Moment((last_login), 'DD-MM-YYYY').format("DD MMM YYYY") || '-'}</Col>
                    </Row>
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><Check width="12" height="12" className="mr-1" />Status</Col>
                      <Col xl={6} className='text-truncate '>{activated || '-'}</Col>
                    </Row>
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold  card-text"><Globe width="12" height="12" className="mr-1" />Language</Col>
                      <Col xl={6} className='text-truncate '>{language_pref === 1 ? "English" : "Arabic"}</Col>
                    </Row>
                  </Col>
                  <Col xl={5} className='p-0'>                  
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><Phone width="12" height="12" className="mr-1" />Contact</Col>
                      <Col xl={6} className='text-truncate '>{mobile.toString().split('').splice(0, 2).join('') === '91' ? <div> <Flag code={ 'IND' } style={{width:'20px', height:'20px'}}/> {mobile.toString().split('').splice(2).join('')} </div> : (mobile.toString().split('').splice(0, 3).join('') === '968') ? <div> <Flag code={ 'OMN' } style={{width:'25px', height:'25px', border:'10px'}}/>{mobile.toString().split('').splice(3).join('')}</div>  : (mobile.toString().split('').splice(0, 3).join('') === '971') ? <div><Flag code={ 'ARE' }  style={{width:'25px', height:'25px', border:'10px'}}/> {mobile.toString().split('').splice(3).join('')} </div> : (mobile.toString().split('').splice(0, 2).join('') === '20') ? <div> <Flag code={ 'EGY' } style={{width:'25px', height:'25px', border:'10px'}}/> {mobile.toString().split('').splice(2).join('')} </div> : (mobile.toString().split('').splice(0, 3).join('') === '973') ? <div> <Flag code={ 'BHR' } style={{width:'25px', height:'25px', border:'10px'}}/> {mobile.toString().split('').splice(3).join('')} </div> : (mobile.toString().split('').splice(0, 3).join('') === '965') ? <div> <Flag code={ 'KWT' } style={{width:'25px', height:'25px', border:'10px'}}/> {mobile.toString().split('').splice(3).join('')} </div> : (mobile.toString().split('').splice(0, 3).join('') === '974') ? <div> <Flag code={ 'QAT' } style={{width:'25px', height:'25px', border:'10px'}}/> {mobile.toString().split('').splice(3).join('')} </div> : (mobile.toString().split('').splice(0, 3).join('') === '966') ? <div> <Flag code={ 'SAU' } style={{width:'25px', height:'25px', border:'10px'}}/> {mobile.toString().split('').splice(3).join('')} </div> : '' || '-'}</Col>
                    </Row>
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><MapPin width="12" height="12" className="mr-1" />City</Col>
                      <Col xl={6} className='text-truncate '>{city || '-'}</Col>
                    </Row>
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><MapPin width="12" height="12" className="mr-1" />State</Col>
                      <Col xl={6} className='text-truncate '>{state || '-'}</Col>
                    </Row>
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><MapPin width="12" height="12" className="mr-1" />Country</Col>
                      <Col xl={6} className=''>{country || '-'}</Col>
                    </Row>
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold mb-1 card-text"><MapPin width="12" height="12" className="mr-1" />Zipcode</Col>
                      <Col xl={6} className='text-truncate '>{zipcode || '-'}</Col>
                    </Row>
                    <Row>
                      <Col xl={6} className="user-info-title font-weight-bold card-text"><MapPin width="12" height="12" className="mr-1" />Address</Col>
                      <Col xl={6} className=''> {getAddress(address1, address2) || '-'}</Col>
                    </Row>
                  </Col>
                </Row>
              </div>
            </div>
          </Row>
        </CardBody>
      </Card>
    )
  }

  if (!store.length) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader blocking />
      </div>
    )
  }

  return (
    <Fragment>
      <BreadCrumbs breadCrumbTitle='Manage Users' breadCrumbParent='Users' breadCrumbActive='Profile' />
      <div className="app-user-view">
        <ProfileCard userInfo={store[1]} userMoney={store[0]?.user_money} />
      </div>
      <Table
        columns={userContents}
        data={store[0]?.user_contests}
        title="Challenges"
      />
      <Table
        columns={userPrizes}
        data={store[0]?.prize_redeemed}
        title="Prize Redeemed"
      />
      <Table
        columns={userFixtures}
        data={store[0]?.fixture_data}
        title="Fixtures"
      />
      <Table
        columns={userReferral}
        data={store[0]?.referral_data}
        title="Referrals"
      />
      <Table
        columns={userTransactions}
        data={store[0]?.user_transactions}
        title="Transactions"
      />
      {/* <Table
        columns={UserMoney}
        data={store[0]?.user_money}
        title="user Money"
      /> */}
      {/* <Table
        columns={userReportStatus}
        data={store[0]?.user_transactions}
        title="Report Status"
      /> */}
    </Fragment>
  )
}

export default Profile
